.process-box {
  background: #fff;
  padding: 10px;
  border-radius: 15px;
  position: relative;
  box-shadow: 2px 2px 7px 0 #00000057;
  margin-bottom: 40px;
}

.process-left:after {
  content: "";
  border-top: 15px solid #ffffff;
  border-bottom: 15px solid #ffffff;
  border-left: 15px solid #ffffff;
  border-right: 15px solid #ffffff;
  display: -ms-inline-grid;
  display: inline-grid;
  position: absolute;
  right: -15px;
  top: 42%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: 3px -2px 3px 0px #00000036;
  z-index: 1;
  display: none;
}

.process-right:after {
  content: "";
  border-top: 15px solid #ffffff00;
  border-bottom: 15px solid #ffffff;
  border-left: 15px solid #ffffff;
  border-right: 15px solid #ffffff00;
  display: -ms-inline-grid;
  display: inline-grid;
  position: absolute;
  left: -15px;
  top: 42%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  box-shadow: -1px 1px 3px 0px #0000001a;
  z-index: 1;
  display: none;
}

.process-step {
  background: #1BE392;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  color: #fff;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 15px;
  position: relative;
  top: -26px;
  border-radius: 0px 0px 10px 10px;
  box-shadow: -6px 8px 0px 0px #00000014;
}

.process-point-right {
  background: #ffffff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 8px solid #00bcd4;
  box-shadow: 0 0 0px 4px #5c5c5c;
  margin: auto 0;
  position: absolute;
  bottom: 40px;
  left: -63px;
}

.process-point-right:before {
  content: "";
  height: 144px;
  width: 11px;
  background: #5c5c5c;
  display: -ms-inline-grid;
  display: inline-grid;
  -webkit-transform: rotate(36deg);
  transform: rotate(36deg);
  position: relative;
  left: -50px;
  top: -0px;
}

.process-point-left {
  background: #ffffff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 8px solid #00bcd4;
  box-shadow: 0 0 0px 4px #5c5c5c;
  margin: auto 0;
  position: absolute;
  bottom: 40px;
  right: -63px;
}

.process-point-left:before {
  content: "";
  height: 144px;
  width: 11px;
  background: #5c5c5c;
  display: -ms-inline-grid;
  display: inline-grid;
  -webkit-transform: rotate(-38deg);
  transform: rotate(-38deg);
  position: relative;
  left: 50px;
  top: 0px;
}

.process-last:before {
  display: none;
}

.process-box p {
  z-index: 9;
}

.process-step p {
  font-size: 20px;
