}
.video-play-btn:after {
  color: #1BE392;
  -webkit-animation-delay: .3s;
    animation-delay: .3s;
}
.video-play-btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
}
.video-play-btn:after, .video-play-btn:before, .video-play-btn:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #1BE392;
  border-radius: 50%;
  box-shadow: 0 0 0 0 currentColor;
  -webkit-animation: button-ripple 3s infinite;
  animation: button-ripple 3s infinite;
  opacity: .6;
  z-index: -1;
}
@-webkit-keyframes button-ripple {
  70% {
    box-shadow: 0 0 0 15px currentColor;
    box-shadow: 0 0 0 var(--glow-size,15px) currentColor;
    opacity: 0;
  }
  
  100% {
    box-shadow: 0 0 0 0 currentColor;
    opacity: 0;
  }
}
@keyframes button-ripple {
  70% {
    box-shadow: 0 0 0 15px currentColor;
    box-shadow: 0 0 0 var(--glow-size,15px) currentColor;
    opacity: 0;
  }
  
  100% {
    box-shadow: 0 0 0 0 currentColor;
    opacity: 0;
  }
}

